import React, { Component } from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Container, Table, InputGroup, FormControl } from 'react-bootstrap';

export class DataRenderer extends Component {
  constructor(props) {
    super(props)
    this.state ={}
  }
    createGroup = async (citycode) => {
      // 4903279
      await fetch("https://sundara-api.herokuapp.com/api/getTimes?citycode=" + citycode)
        .then(async (response) => {
          response = await response.json();
            let group = []
          for (let i = 0; i < response.goodTimes.length; i++) {
            group.push(
              <tr><td>{response.goodTimes[i][0]}</td><td>{response.goodTimes[i][1]}</td></tr>
            )
          }
          this.setState({Times: group})
          console.log(group)
      })
    }
    render() {
      return (
      <Container>
          <InputGroup className="mb-3">
            <InputGroup.Prepend>
              <InputGroup.Text id="basic-addon3">
                City GeoCoder ID
              </InputGroup.Text>
            </InputGroup.Prepend>
            <FormControl onChange={(event) => {this.createGroup(event.target.value)}} id="basic-url" aria-describedby="basic-addon3" />
          </InputGroup>
        <br />
        <div aria-label="Times list" key={'opts'}>
          <Table striped bordered hover>
            <thead>
              <tr>
                <th>Start Time</th>
                <th>End Time</th>
              </tr>
            </thead>
            <tbody>
              {this.state.Times}
            </tbody>
          </Table>
        </div>
      </Container>
      );
    }
}